import { Sparklines,SparklinesLine } from 'react-sparklines';
import './Chart.scss'
export function Chart({data}) {
        return (
            <div className="chart">
                {data &&<Sparklines data={data}  width={200} height={200} margin={5}>
                <SparklinesLine color="blue" />
                </Sparklines>}

            </div>
        )
}
