
import { Component } from 'react'
import contactService from '../../services/contact.service'
import {NavLink} from 'react-router-dom'
import remove from '../../assets/img/icons/delete.png'
import edit from '../../assets/img/icons/edit.png'


import './ContactDetails.scss'

export class ContactDetails extends Component {
    state = {
        contact: null
    }
    componentWillMount() {
        this.loadContact(this.props.match.params.id)
    }
    loadContact = async (id) => {
        const contact = await contactService.getContactById(id)
        this.setState({ contact })
    }
    async onRemoveContact(){
        const {contact} = this.state
        console.log(contact);
        await contactService.deleteContact(contact._id)
        this.props.history.push('/contact')
    }
    async onEditContact(){
        const {contact} = this.state
        this.props.history.push('/contact/edit/' + contact._id)
    }

    render() {
        const { contact } = this.state
        if (!contact) return <h2>Loading..</h2>
        return (
            <div className="contact-details">
                <NavLink to="/contact">Back</NavLink>
                <div>
                    <img src={`https://robohash.org/${contact._id}`} alt="" />
                    <p>Name: {contact.name}</p>
                    <p>Email: {contact.email}</p>
                    <p>Phone: {contact.phone}</p>
                    <div className="actions">
                        <button onClick={() => this.onRemoveContact()} ><img src={remove} alt=""/></button>
                        <button onClick={() => this.onEditContact()} ><img src={edit} alt=""/></button>
                    </div>
                </div>
            </div>
        )
    }
}
